import { ENTITY_UPDATE, ENTITY_DELETE, ENTITY_CREATE } from './types.js';

export function updateEntity(itemType, itemID, newItemAttributes) {
  return {
    type: ENTITY_UPDATE,
    payload: {
      itemType: itemType,
      itemID: itemID,
      newItemAttributes: newItemAttributes
    }
  };
}

export function deleteEntity(itemType, itemID) {
  return {
    type: ENTITY_DELETE,
    payload: { itemType: itemType, itemID: itemID }
  };
}

export function createEntity(itemType, newItemAttributes) {
  return {
    type: ENTITY_CREATE,
    payload: {
      itemType: itemType,
      newItemAttributes: newItemAttributes
    }
  };
}
